<template>
    <div id="app">
        <h1>Random Gif Cat</h1>
        <div class="container">
            <div class="textBox">   
                <p>Titulo:</p>
                <p>Filtro:</p>
                <p>Color:</p>
                <p>Tamaño:</p>
            </div>
            <form class="formBox">  
                <input type="text" v-model="data_say">
                <select @change="change_filter($event)">
                    <option v-for="(filtro, index) in list_filters" v-bind:key="index" v-bind:value="filtro" v-text="filtro">Value 1</option>
                </select>  
                <div style="display: flex">
                    <select @change="change_color($event)">
                        <option v-for="(color, index) in list_colors" v-bind:key="index" :value="color" v-text="list_colors_text[index]">Value 1</option>
                    </select>
                    <div class="circle" v-bind:style="{ 'background-color': data_color }"></div>
                </div>
                <input type="number" v-model="data_size">
            </form>
        </div>
        <button v-on:click="search_cat" style="margin-top: 32px;">Buscar mi gatito (Puede tardar un poco)</button>
        <div v-if="data_image_url">
            <img v-bind:src="data_image_url" alt="cat-alt">    
        </div>          
    </div>
</template>
<script>
export default 
{ 
    name: 'App',
    data() 
    {
        return {
            data_say: "Example text",
            data_color: "red",
            data_filter: "blur", 
            data_size: "100",
            data_image_url: "",
            list_filters: ["blur", "mono", "sepia", "negative", "paint", "pixel"],
            list_colors: ["red", "green", "blue", "cyan", "yellow"],
            list_colors_text: ["Rojo", "Verde", "Azul", "Celeste", "Amarillo"]
        }
    },
    methods:
    {  
        search_cat() 
        {
            fetch(`https://cataas.com/cat/gif/says/${this.data_say}?filter=${this.data_filter}&color=${this.data_color}&size=${this.data_size}`)
            .then(response =>  
            {
                this.data_image_url = response.url
            });
        }, 
        change_filter(event)
        {
            this.data_filter = event.target.value
        },
        change_color(event)
        {
            this.data_color = event.target.value
        }
    }, 
}
</script>
<style>
* { padding: 0; margin: 0; }
#app { font-family: Arial, Helvetica, sans-serif; display: flex; flex-direction: column; align-items: center; }
.container { background-color: indianred; display: flex; flex-direction: row; width: 100%; }
body { background-color: lightblue; }
h1, img { padding: 32px; }
p { margin: 8px; }
input { width: 144px; margin: 8px; }
select { width: 96px; margin: 8px; }
.textBox { width: 50%; display: flex; flex-direction: column; align-items: flex-end; padding: 8px; }
.formBox { width: 50%; display: flex; flex-direction: column; padding: 8px; }
.circle { margin: 8px; border: solid 1px black;height: 16px; width: 16px; border-radius: 50%; z-index: 32; }
</style>
